              <v-subheader>GRAPH HEIGHT</v-subheader>
              <v-list-item>
                <v-slider
                  v-model="graphHeight"
                  thumb-label="always"
                  max="1280"
                  min="500"
                  v-on:change="search"
                ></v-slider>
